package com.example.redwood.emojisearch.compose

import androidx.compose.runtime.Composable
import app.cash.redwood.LayoutModifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.compose.RedwoodComposeNode
import com.example.redwood.emojisearch.widget.EmojiSearchWidgetFactoryProvider
import com.example.redwood.emojisearch.widget.TextInput
import example.values.TextFieldState
import kotlin.OptIn
import kotlin.String
import kotlin.Unit

@Composable
@OptIn(RedwoodCodegenApi::class)
public fun TextInput(
  state: TextFieldState = TextFieldState(),
  hint: String = "",
  onChange: ((TextFieldState) -> Unit)? = null,
  layoutModifier: LayoutModifier = LayoutModifier,
): Unit {
  RedwoodComposeNode<EmojiSearchWidgetFactoryProvider<*>, TextInput<*>>(
      factory = { it.EmojiSearch.TextInput() },
      update = {
        set(layoutModifier) { layoutModifiers = it }
        set(state, TextInput<*>::state)
        set(hint, TextInput<*>::hint)
        set(onChange, TextInput<*>::onChange)
      },
      content = {
      },
      )
}
